            dni: this.dni
            }).then(response => {
                var json = {msg: 'Error'};
                if(JSON.stringify(response.data) == JSON.stringify(json)){
                    alert("Datos mal introducidos");
                }
                else{
                    console.log(response.data);
                    this.userName = response.data;
                }
            })
        },
       
       /****** COMPROBAR INICIO DE SESI√ìN ******/
