            console.log(this.dni);
            console.log(this.contrase√±a);
            this.alerta = false;
            if (this.dni == "" && this.contrase√±a == "") {
                this.tipoAlerta = "error";
                this.alerta = true;
                this.textoAlerta = "Por favor, introduce usuario y contrase√±a";
            } else if (this.dni == "") {
                this.tipoAlerta = "error";
                this.alerta = true;

                this.textoAlerta = "Por favor, introduce tu usuario";
            } else if (this.contrasen√±a == "") {
